import React from "react"
import {Link} from "react-router-dom"
import ApplicationsData from "./ApplicationsData"

const Home = ({en, copyToClipboard}) => {

    return (
        <section className="container">
            <div className="d-flex flex-column">
                <div className="d-flex flex-column home-section">
                    <h1>{en ? "About me" : "O mně"}</h1>
                    <p className="home-paragraph">{en ? "My name is Dan Sprucek and I've been learning programming since beginning of 2020. So far my journey included:" : "Jmenuji se Dan Špruček a programování se věnuji od začátku roku 2020. Moje cesta vypadá zatím následovně:"}</p>
                    <div className="path-container">
                        <ul className="home-techlist">
                            <li>HTML5</li>
                            <li>CSS</li>
                            <li>JavaScript</li>
                            <li>jQuery</li>
                            <li>Sass</li>
                            <li>Bootstrap</li>
                            <li>ReactJS</li>
                        </ul>
                        <img className="path-img" src="./sources/Path.png" alt="Learning Path" title="Learning Path"></img>
                    </div>
                    <h5>{en ? "Would you like to know more about me?" : "Chcete se dozvědět více?"}</h5>
                    <Link className="home-links" to="/about" title="About Page">{en ? "GO TO ABOUT PAGE" : "VICE NA 'O MNĚ'"}</Link>
                </div>
                <div className="d-flex flex-column home-section home-midsection">
                    <h1>Portfolio</h1>
                    <h4>{en ? "Here is a sample of my work:" : "Ukázka mých projektů:"}</h4>
                    <div className="home-portfolio-container">
                        <div className="d-flex flex-column mr-1">
                            <a href={ApplicationsData[0].url} target="_blank" rel="noopener noreferrer"><img className="home-portfolio-example" src={`./sources/${ApplicationsData[0].name}.png`} alt={ApplicationsData[0].name} title={ApplicationsData[0].name}></img></a>
                            <h4 className="mt-2">{ApplicationsData[0].name}</h4>
                        </div>
                        <div className="d-flex flex-column ml-1">
                            <a href={ApplicationsData[1].url} target="_blank" rel="noopener noreferrer"><img className="home-portfolio-example" src={`./sources/${ApplicationsData[1].name}.png`} alt={ApplicationsData[1].name} title={ApplicationsData[1].name}></img></a>
                            <h4 className="mt-2">{ApplicationsData[1].name}</h4>
                        </div>
                    </div>
                    <h5>{en ? "Would you like to know more about my work?" : "Chcete vědět více o mých projektech?"}</h5>
                    <Link className="home-links" to="/portfolio" title="Portfolio Page">{en ? "GO TO PORTFOLIO PAGE" : "VÍCE NA 'PORTFOLIO'"}</Link>
                </div>
                <div className="d-flex flex-column home-section">
                    <h1>{en ? "Contact" : "Kontakt"}</h1>
                    <h6>{en ? "Best way to contact me is via e-mail:" : "Nejlepší způsob jak mě kontaktovat je přes e-mail:"} <span><a href="mailto:dan.sprucek@gmail.com" title="Send me an e-mail">dan.sprucek@gmail.com</a> <i className="far fa-copy" data-value="dan.sprucek@gmail.com" onClick={copyToClipboard} title="Copy to clipboard"></i></span></h6>
                    <h5>{en ? "Would you like to know more ways to contact me?" : "Chcete vědět další způsoby jak mě kontaktovat?"}</h5>
                    <Link className="home-links" to="/contact" title="Contact Page">{en ? "GO TO CONTACT PAGE" : "VÍCE NA 'KONTAKT'"}</Link>
                </div>
            </div>
        </section>
    )
}

export default Home