import React from "react"

const About = ({handleScroll}) => {

    return (
        <section className="container">
            <h1>Basic Information</h1>
            <div className="d-flex justify-content-around mb-3 pb-3">
                <div className="d-flex justify-content-between">
                    <div className="subsection-container-50PER d-flex flex-column justify-content-between align-items-center">
                        <img id="photo-dan" src="./sources/CV.jpg" alt="Dan Sprucek" title="Dan Sprucek"></img>
                        <a className="download-cv download-cv-mobile" href="https://drive.google.com/file/d/1ZcU5-H9zAH93qZ5IUYBiu321HDW24axe/view?usp=sharing" target="_blank" rel="noopener noreferrer">DOWNLOAD CV</a>
                    </div>
                    <div className="subsection-container-50PER">
                        <dl className="mb-0">
                            <dt>Name</dt>
                            <dd>Dan Sprucek</dd>
                            <dt>Location</dt>
                            <dd>Prague, CZ</dd>
                            <dt>Likes</dt>
                            <dd>Coding, Sport, Board Games</dd>
                            <dt>Humor</dt>
                            <dd className="mb-0">Limit = 404 Not Found</dd>
                        </dl>
                    </div>
                </div>
                <div className="about-further-info">
                    <h5>Certificates</h5>
                    <ul>
                        <li title="Issued by freeCodeCamp.com">Responsive Web Design</li>
                        <li title="Issued by freeCodeCamp.com">JavaScript Algorithms and Data Structures</li>
                        <li title="Issued by freeCodeCamp.com">Front End Libraries</li>
                    </ul>
                    <h5>Curriculum Vitae</h5>
                    <a className="download-cv download-cv-desktop" href="https://drive.google.com/file/d/1ZcU5-H9zAH93qZ5IUYBiu321HDW24axe/view?usp=sharing" target="_blank" rel="noopener noreferrer">DOWNLOAD CV</a>
                </div>
            </div>
            <div>
                <h2>Would you like to know more?</h2>
                <div className="d-flex flex-column align-items-center">
                    <span className="about-scroll" onClick={handleScroll} data-value="abroad" title="Two years abroad"><i className="fas fa-long-arrow-alt-right"></i> I spent 2 years abroad <i className="fas fa-long-arrow-alt-left"></i></span>
                    <span className="about-scroll" onClick={handleScroll} data-value="pmWork" title="Three years as a Project Manager"><i className="fas fa-long-arrow-alt-right"></i> Project Manager for 3 years <i className="fas fa-long-arrow-alt-left"></i></span>
                    <span className="about-scroll" onClick={handleScroll} data-value="programStart" title="Started with Programming in 2020"><i className="fas fa-long-arrow-alt-right"></i> Programming since 2020 <i className="fas fa-long-arrow-alt-left"></i></span>
                </div>
                <h4 id="abroad" className="about-experience-heading">Two years abroad</h4>
                <div>
                    <p>First time in 2015 - went to the <span className="font-weight-bold">Hague in the Netherlands</span>. Started as Administrative/IT support voluntary in <abbr title="Non-Governmental Organisation">NGO</abbr>, then found a job as a <span className="font-weight-bold">Billing assistant</span> in Astrium Company.</p>
                    <p>Second time in 2017 - moved to <span className="font-weight-bold">Edinburgh in Scotland</span>. After a brief Bartender/Waiter gig I joined a hotel staff as a <span className="font-weight-bold">Night Auditor</span> in newly opening Adagio Aparthotel.</p>
                </div>
                <h4 id="pmWork" className="about-experience-heading">Three years as a Project Manager</h4>
                <div>
                    <p>First time in 2016 - right after coming back from the Netherlands found a job as <span className="font-weight-bold">Project Manager in Wunderman Company</span>. Great people, interesting job, learnt a lot about online marketing and processes.</p>
                    <p>Second time in 2018-2020 - again after coming back from abroad, this time Scotland. Found most of my old team, gradually gained more experience, responsibilities, becoming <span className="font-weight-bold">Senior Project Manager</span> towards the end.</p>
                </div>
                <h4 id="programStart" className="about-experience-heading">Started with Programming in 2020</h4>
                <div>
                    <p>At the beginning of 2020 I quit my job right when CoronaVirus hit the world. I had a dream of learning Programming all my life, so I took this "opportunity" and started learning.</p>
                    <p>Started with Giraffe Academy video tutorials, Greenfox Academy free 3-weeks course and first freeCodeCamp certificate to learn <span className="font-weight-bold">HTML, CSS, JavaScript and jQuery</span>. Created my first website (not mobile friendly though). Continued with freeCodeCamp certificates and other sources to learn <span className="font-weight-bold">Bootstrap, Sass and ReactJS</span>.</p>
                    <p>Joined my first <span className="font-weight-bold">Hacathon</span> in July to contribute as a Frontend Developer.</p>
                </div>
            </div>
        </section>
    )
}

export default About